      .image{
        margin: 1rem 0;
      }
      .item-info{
        .title, .description{
          width: 15rem;
        }
      }
    }
    .cartPreview{
      max-width: 70vw;
      margin-bottom:3rem;
    }
  }
  .Cart{
    // height: 100vh;
    .msg{
      text-decoration: none;
      margin:2rem 1rem;
    }
    .container{
      .items{
        margin: 1rem 0;
        .item{
          grid-template-columns: 5% 53% 20% 20%;
          .info{
            .title{
              font-size: 0.8rem;
              font-weight: 300;
            }
          }
        }
      }
    }
  }
  .About{
    div .title{
      margin: 3rem 0;
    }
    .container{
      grid-template-columns: 1fr;
      #designer-image{
        grid-column-start: initial;
        grid-row-start: initial;
      }
    }
  }
  .Contact{
    .header{
      font-size: 2rem;
    }
    .container{
      .mini-header{
        font-size: 1.3rem;
      }
      .line{
        width: 10%;
      }
      .info{
        .store, .hours,.contact{
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          .title{
            font-size: 1.3rem;
            max-width: 70%;
            margin: 0 auto;
          }
          .text{
            font-size: .825rem;
            font-weight: 200;
            max-width: 80%;
          }
        }
      }
      .inquiries p{
        font-size: .8rem;
        max-width: 70%;
      }
      .form form{
        margin: 0 1rem;
        max-width: 100%;
      }
    }
  }
  .Footer{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .container{
      grid-template-columns: 1fr;
      text-align:center;
      grid-gap: 1.5rem;
      .links{
        grid-template-columns: inherit;
      }
    }
    }
}
@media screen and (max-width:1024px){
  .Home{
    .carousel{
      .Product{
        .container{
          .items{
            grid-auto-columns: 30%;
          }
        }
      }
    }
  }
}
@media screen and (max-width:768px){
  .Home{
    .carousel{
      .Product{
        .container{
          .items{
            grid-auto-columns: 40%;
          }
        }
      }
    }
  }
}